html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}

/* HTML5 display-role reset for older browsers */
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block;
}

body {
  line-height: 1;
}

ol,
ul {
  list-style: none;
}

blockquote,
q {
  quotes: none;
}

blockquote:before,
blockquote:after,
q:before,
q:after {
  content: "";
  content: none;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

/* Base
-------------------------------------------------------------- */
/* Colors
-------------------------------------------------------------- */
/* Widths & spacing
-------------------------------------------------------------- */
html {
  background-color: #fcfafa;
}

/* Typography styles
-------------------------------------------------------------- */
body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  -moz-font-feature-settings: "liga";
  text-rendering: optimizeLegibility;
  font-size: 16px;
  line-height: 1.5;
  color: #514f4d;
  font-family: system-ui, sans-serif;
}
body h1 {
  font-size: 4.6875rem;
  line-height: 1;
  font-weight: 700;
  letter-spacing: -0.1125rem;
}
body h2,
body h3 {
  font-size: 1.25rem;
  font-weight: 700;
  letter-spacing: -0.0125rem;
  margin-bottom: 0.9375rem;
}
body p {
  margin-bottom: 0.9375rem;
}
body a {
  color: #514f4d;
  text-decoration: underline;
  display: inline-block;
}
body a:hover {
  text-decoration: none;
}
body b,
body strong {
  font-weight: 600;
}
body em {
  font-style: italic;
}

/* Structure
-------------------------------------------------------------- */
.container-outer {
  max-width: 109.375rem;
  padding: 0 6rem;
  margin: 0 auto;
}

.txt-container-small {
  max-width: 40.625rem;
}

/* Colors
-------------------------------------------------------------- */
.gfm-green {
  color: #00a671;
}

.lr-blue {
  color: #008df9;
}

.cu-pink {
  color: #fd71af;
}

.v-blue {
  color: #003f5f;
}

.il-red {
  color: #e15030;
}

/* Typography
-------------------------------------------------------------- */ /* Global gravity forms styles
-------------------------------------------------------------- */
#g-form .gform_wrapper .gform_fields {
  column-gap: 1rem;
}
#g-form .gform_wrapper input::placeholder,
#g-form .gform_wrapper textarea::placeholder {
  color: #514f4d !important;
}
#g-form .gform_wrapper input,
#g-form .gform_wrapper textarea {
  border: none;
  border-bottom: 0.0625rem solid #514f4d;
  background-color: transparent;
  padding: 1rem;
  font-family: system-ui, sans-serif;
  color: #514f4d;
  font-size: 1.25rem;
  line-height: 1.875rem;
}
#g-form .gform_wrapper textarea {
  height: 12.5rem;
  resize: none;
}
#g-form .gform_wrapper .gform_footer {
  justify-content: flex-end;
}
#g-form .gform_wrapper .gform_footer .gform_button {
  margin-bottom: 0;
  padding: 0;
  border: none;
  text-decoration: underline;
  cursor: pointer;
  color: #514f4d;
}
#g-form .gform_wrapper .gform_footer .gform_button:hover {
  text-decoration: none;
}

/* Box size border box
-------------------------------------------------------------- */
html {
  box-sizing: border-box;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}